.fullScreen {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-color: #fff;
}
.menu-box .van-hairline--bottom::after {
  border-bottom-width: 0;
}
.menu-box .van-nav-bar__title {
  color: #f5f5f5;
}
.menu-box .van-nav-bar {
  width: 100%;
  left: 0;
  top: 0;
  background-color: transparent;
}
.menu-box .van-nav-bar .van-ellipsis {
  overflow: inherit;
}
.menu-box .van-nav-bar .van-icon {
  color: #23e379;
}
.menu-box .van-nav-bar__text {
  color: #23e379;
}
.menu-detail .vux-bg-blur {
  z-index: -2;
  opacity: 1;
  position: absolute;
  display: block;
  max-height: none;
  left: 0;
  top: 0;
  width: 100%;
  height: 5rem;
  -webkit-transition: opacity linear 0.8s;
  transition: opacity linear 0.8s;
  filter: blur(6px);
  background-size: cover !important;
}
.menu-detail .menu-box {
  padding-top: 1.5rem;
  padding-bottom: 0.5rem;
}
.menu-detail .menu-box .van-nav-bar__text:active {
  background-color: transparent;
}
.menu-detail .menu-box .van-card {
  background-color: transparent;
  z-index: 2;
}
.menu-detail .menu-box .van-card .van-card__content {
  padding-left: 0.48rem;
}
.menu-detail .menu-box .van-card .van-card__title {
  font-size: 14px;
}
.menu-detail .menu-box .van-card .van-card__desc {
  padding-top: 0.18rem;
  color: #f5f5f5;
}
.menu-detail .menu-box .van-card .van-card__price {
  color: #f5f5f5;
  font-weight: normal;
}
.menu-detail .menu-box .van-card .van-tag {
  margin-right: 0.1rem;
}
.menu-detail .menu-box .van-card .van-card__num {
  float: inherit;
  margin-top: 0.2rem;
}
.menu-detail .menu-box .van-card .thumb-img {
  width: 2.8rem;
  height: auto;
  border-radius: 4px;
  z-index: 2;
}
.menu-detail .menu-box .van-card .thumb-img img {
  border-radius: 4px;
}
.menu-detail .menu-box .van-card .van-card__thumb:after {
  content: "";
  position: absolute;
  width: 2.78rem;
  height: 2.78rem;
  margin-right: 0.48rem;
  border: 1px #23e379 solid;
  transform: rotate(5deg);
  border-radius: 4px;
  background: rgba(35, 227, 121, 0.2);
  transform-origin: right bottom;
  left: -2px;
  top: 0px;
}
.song-list-menu .van-nav-bar__arrow {
  color: #23e379;
}
.song-list-menu .van-nav-bar__text {
  color: #333;
}
.song-list-menu .van-nav-bar__text span {
  color: #7d7e80;
  font-size: 12px;
}
.song-list-menu .van-address-item {
  padding: 10px 15px;
}
.song-list-menu .van-address-item__edit {
  color: #23e379;
}
.song-list-menu .song-list-scroll {
  height: 52vh;
  overflow: hidden;
}
